import React from 'react';
import ReactDOM from 'react-dom';
import { HashRouter, Routes, Route, Navigate, } from "react-router-dom";
import Header from './Header.jsx';
import Activities from './components/Activities';
import IncomingCalls from './components/IncomingCalls';
import CallDetail from './components/CallDetail/index.js';
import ArchivedCalls from './components/ArchiveCalls/ArchiveCalls.js';

const App = () => {
  return (
    <div className='container'>
      <Header/>
      <HashRouter basename='/'>
        <Routes>
          <Route path='/' element={<Navigate to ="/activities" />}/>
          <Route path='/incomingcalls' exact element={<IncomingCalls />} />
          <Route path='/calls/:id' element={<CallDetail /> } />
          <Route path='/activities' exact element={<Activities />} />
          <Route path='/archivedcalls' exact element={<ArchivedCalls />} />
          <Route path='*' exact element={<h1>404: Page not found</h1>} />
        </Routes>
      </HashRouter>
    </div>
  );
};

ReactDOM.render(<App/>, document.getElementById('app'));

export default App;
